    /**
     * @des 收货地址列表
     *
     * @author stav stavyan@qq.com
     * @date 2020-03-10 18:00
     * @copyright 2019
     */
    import {addressDelete, addressList} from "@/api/userInfo";
    import empty from "@/components/empty";
    import rfLoadMore from '@/components/rf-load-more/rf-load-more';

    export default {
        components: {
            empty,
            rfLoadMore
        },
        data() {
            return {
                timeOutEvent: 0,
                source: 0,
                page: 1,
                addressList: [],
                loadingType: 'more',
                loading: true,
				userInfo:{},
				token:''
            }
        },
        //下拉刷新
        onPullDownRefresh() {
            this.page = 1;
            this.addressList = [];
            this.getAddressList('refresh');
        },
        //加载更多
        onReachBottom() {
            this.page++;
            this.getAddressList();
        },
        onLoad(option) {
            this.source = option.source;
        },
        onShow() {
            this.initData()
        },
        methods: {
            goTouchStart(id) {
                clearTimeout(this.timeOutEvent);//清除定时器
                this.timeOutEvent = 0;
                this.timeOutEvent = setTimeout(() => {
                    uni.showModal({
                        content: '确定要删除该收货地址吗',
                        success: (e) => {
                            if (e.confirm) {
                                this.$del(`${addressDelete}?id=${id}`).then(() => {
                                    this.page = 1;
                                    this.addressList.length = 0;
                                    this.getAddressList();
                                })
                            }
                        }
                    });
                }, 0.5 * 1000);//这里设置定时
            },
            //手释放，如果在500毫秒内就释放，则取消长按事件，此时可以执行onclick应该执行的事件
            goTouchEnd() {
                clearTimeout(this.timeOutEvent);
                if (this.timeOutEvent != 0) {
                }
            },
            //如果手指有移动，则取消所有事件，此时说明用户只是要移动而不是长按
            goTouchMove() {
                clearTimeout(this.timeOutEvent);//清除定时器
                this.timeOutEvent = 0;
            },
            // 数据初始化
            initData() {
                this.page = 1;
                this.addressList.length = 0;
				this.userInfo=uni.getStorageSync('userInfo')
				this.token=this.userInfo.id
				if(this.token){
					 this.getAddressList();
				}else{
					uni.navigateTo({
						url: "/pages/public/login",
					})
				}
               
            },
            // 获取收货地址列表
            async getAddressList(type) {

                await this.$get('http://localhost:8080/static/api/addressList.json', {
                    page: this.page
                }).then(r => {
                    this.loading = false;
                    if (type === 'refresh') {
                        uni.stopPullDownRefresh();
                    }
                    this.loadingType = r.data.length === 10 ? 'more' : 'nomore';
                    this.addressList = [...this.addressList, ...r.data];
                }).catch(() => {
                    this.loading = false;
                    if (type === 'refresh') {
                        uni.stopPullDownRefresh();
                    }
                });
            },
            // 选择地址
            checkAddress(item) {
                if (this.source == 1) {
                    //this.$api.prePage()获取上一页实例，在App.vue定义
                    this.$api.prePage().addressData = item;
                    uni.navigateBack()
                }
            },
            // 跳转添加地址页面
            addAddress(type, item) {
                uni.navigateTo({
                    url: `/pages/address/addressManage?type=${type}&id=${item && item.id || undefined}`
                })
            },
        }
    }
